import React, {useState} from 'react';
import './SpecialOffer.css'

export function SpecialOffer () {

    const [email, setEmail] = useState('') //   email
    const [error, setError] = useState('') //   
    const [isLoading, setIsLoading] = useState(false) //  
    const [message, setMessage] = useState('') //  
    const [isTouched, setIsTouched] = useState(false); //     

    //    email
    const validateEmail = (email) => {
        const regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/; //   email
        if (!email.trim()) {
            return '    ';
        } else if (!regex.test(email)) {
            return '  email';
        }
        return '';
    }

    //   email
    const handleEmailChange = (e) => {
        const value = e.target.value;
        setEmail(value);

        if (isTouched) { //      
            setError(validateEmail(value));
        }

        //     
        setMessage('');
    };

    const handleBlur = () => {
        setIsTouched(true);
        setError(validateEmail(email));
    }

    //   
    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!isTouched || error || !email.trim()) return;

        setIsLoading(true)
        setMessage(''); //  

        try{
            await new Promise((resolve) => setTimeout(resolve, 2000));

            setMessage('  !');
            setEmail('');
            setIsTouched(false);
        } catch (err) {
            setMessage('   .  ');
        } finally {
            setIsLoading(false);
        }
    };

    // ,   email
    const isEmailValid = !error && email.trim() !== '';

    return (
        <div className='offer'>
            <div className='women_img'></div>
            <div className='men_img'></div>
            <div className='offer_container'>
                <p className='offer_container_p'>Special Offer</p>
                <span>Subscribe <br/>And Get 10% Off</span>
                <form onSubmit={handleSubmit}>
                    <input
                        type='email'
                        placeholder="Enter your email"
                        value={email}
                        onChange={handleEmailChange}
                        onBlur={handleBlur} //    
                        disabled={isLoading} //    
                    />
                    {isTouched && error && <p style={{color: 'red'}}>{error}</p>}
                    {message && <p style={{color: message.includes('') ? 'red' : 'green'}}>{message}</p>}
                    <button
                        type='submit'
                        disabled={!isEmailValid || isLoading}>
                        {isLoading ? 'Sending...' : 'Subscribe'}
                    </button>
                </form>
            </div>
        </div>
    );
}