import React from 'react';

export function StarRating({rating}) {

    const totalStars = 5; //   
    const filledStars = Math.round(rating); //   ()

    return (
        <div className='star_rating'>
            {[...Array(totalStars)].map((_, index) => (
                <span
                    key={index}
                    className={index < filledStars ? 'star_filled' : 'star'}>
                    </span>
            ))}
        </div>
    );
}